      .sort{
        color: $green;
        border-radius: 50px;
        font-size: 18px;
        font-weight: 500;
        @include wh(100%,80px);
        background-color: #DCDCDC;
        @include flex(row,center,center)
        i{
          font-size:30px
        }
      }
    }

    /* 弹出框*/
    .sortBar{
      width: 100%;
      // padding: 0 25px;
      height: 0;
      transition: height .1s;
      overflow: hidden;
      font-size: 18px;
      background-color: #DCDCDC;
      color:rgb(152,152,152);
      @include flex(column,space-around,center)
      span{
        flex-grow : 1;
        @include wh(146px,60px)
        @include flex(row,center,center)
      }
    }
    /* 排序兰的div 显示时候的样式*/
    .showSortBar{
      height: 200px;
    }
    /* 选中的文字样式*/
    .active{
      color:$green;
    }

  }
